'use client'
import { useContext, useEffect, useState } from "react";
import { TemplateContext } from "./template-provider";
import {useTemplateContext} from './useTemplateContext'
export type Template = {
  id?: number,
  name:string,
  content:string
}
export default function Home() {
  const {state,dispatch} = useTemplateContext()
  useRouter
  useEffect(()=>{
    const fetchTemplates = async() => {
      try {
        dispatch({type:'FETCH_REQ'})
        const res = await fetch('http://127.0.0.1:8001/api/templates/');
        if (!res.ok){
          throw new Error("Error in fetching templates")
        }
        const data = await res.json();
        dispatch({type:'SUCCESS',payload:data})
      } catch (error) {
        console.log(error)
        dispatch({type:'FAILED',payload:error})
      }
    }
    fetchTemplates()
  },[])
  const handleOnclick = () => {

  }
  return (
    <div>
      <h1>Templates</h1>
{state.isLoading ? (
  <p>Loading...</p>
) : state.errors.length > 0 ? ( // ✅ Check for errors first
  <div>
    <p>Error: {state.errors || "Something went wrong!"}</p>
  </div>
) : (
  state.templates.length > 0 ? (
    state.templates.map((template:Template) => (
      <div key={template.id} onClick={handleOnclick}>
        <h2>{template.name}</h2>
        {/* Render HTML content safely */}
        <div dangerouslySetInnerHTML={{ __html: template.content }} />
      </div>
    ))
  ) : (
    <p>No templates available.</p> // ✅ Handle empty templates case
  )
)}
    </div>
  );
}
